import React from 'react';
import Box from '@mui/material/Box';
import Typography from '@mui/material/Typography';
import Button from '@mui/material/Button';
import ButtonGroup from '@mui/material/ButtonGroup';
import LinkedInIcon from '@mui/icons-material/LinkedIn';
import GitHubIcon from '@mui/icons-material/GitHub';

function Feed(){
    return (        <>
        <div style={{display: 'flex', alignItems:'center'}}>
        <Box
          m="auto"  
          component="img"
          sx={{
            height: 233,
            width: 350,
            maxHeight: { xs: 233, md: 167 },
            maxWidth: { xs: 350, md: 250 },
            borderRadius: 5,
            padding: 5
          }}
          src="https://placehold.co/600x400"
        />
        </div>
         <div style={{display: 'flex', alignItems:'center'}}>
         <Box m="auto"  >
         <Typography variant="h6" component="div" color="grey.900" style={{ fontFamily: '"Segoe UI"', fontWeight:'bold' }}>Cedez Marie Gulane</Typography>
         <Typography variant="h8" component="div" color="grey.900" style={{ fontFamily: '"Segoe UI"', fontWeight:'bold' }} style={{ fontFamily: '"Segoe UI"', fontWeight:'bold' }}>Fulltime Software Developer, Part-time furmom</Typography>
         </Box>
        </div>
            <Box
              sx={{
                display: 'flex',
                flexDirection: 'column',
                alignItems: 'center',
                '& > *': {
                  m: 1,
                },
              }}
            >
              <ButtonGroup variant="text" aria-label="text button group">
                <Button href="https://www.linkedin.com/in/cedez-marie-gulane-427199110/" ><LinkedInIcon></LinkedInIcon></Button>
                <Button href="https://github.com/Sedce/"><GitHubIcon></GitHubIcon></Button>
                <Button>Three</Button>
              </ButtonGroup>
            </Box>
        </>

    );
};
export default Feed;