import React from 'react';
import { makeStyles } from '@mui/styles';
import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import Button from '@mui/material/Button';
import IconButton from '@mui/material/IconButton';
import { grey } from '@mui/material/colors';

const useStyles = makeStyles((theme) => ({
appbar: {
     background : 'black'
},
}));

function Header(){
    const classes = useStyles()
    return (<Box sx={{ flexGrow: 1 }}>
      <AppBar position="static" style={{ background: 'transparent' }}>
        <Toolbar>
          <IconButton
            size="large"
            edge="start"
            color="inherit"
            aria-label="menu"
            sx={{ mr: 2 }}
          >
          </IconButton>
          <Typography variant="h6" component="div" sx={{ flexGrow: 1 }}>
            
          </Typography>
          <Button color="secondary"><Typography color="grey.900" style={{ fontFamily: '"Segoe UI"', fontWeight:'bold' }}>Home</Typography></Button>
          <Button color="secondary"><Typography color="grey.900" style={{ fontFamily: '"Segoe UI"', fontWeight:'bold' }}>Resume</Typography></Button>
          <Button color="secondary"><Typography color="grey.900" style={{ fontFamily: '"Segoe UI"', fontWeight:'bold' }}>Projects</Typography></Button>
        </Toolbar>
      </AppBar>
    </Box>
    );
};
export default Header;